.footer {
  display: block;
  background-color: $footer-bg;
  max-width: 1200px;
  margin: 0 auto;
  > .container-max {
    background-color: $footer-bg;
    hr {
      border-top: 1px solid white;
    }
  }

  a {
    color: $footer-color;
  }

  .svg.plenty-brand {
    padding: 0 .5em;
    path {
      fill: $white;
    }
  }

  .footer-features {
    border-top: 0;
    background-color: $footer-features-bg;
    color: $footer-features-color;
    i {
      color: $footer-features-color !important;
    }
  }

  .footer-content {

  }

  .footer-services {
    display: none; // flex;
    flex-flow: column nowrap;

    .services-payment,
    .services-shipping,
    .services-certificate {
      display: inline-block;
      display: inline-flex;
      flex-flow: row wrap;
      flex: 0 1 auto;
    }

    .services-certificate {
      flex: 1 0 auto;
    }

    .services-title {
      width: 100%;
    }

    a {
      flex: 1 1 auto;
      display: inline-block;
      max-width: 5rem;
      margin-right: .5rem;
    }

    img {
      max-height: 4rem;
    }
  }

  .footer-column {
    font-family: $font-family-heading;

    h5 {
      font-weight: 700;
      color: $white;
    }
    ul {
      li {
        a {
          color: $white;
        }
      }
    }
  }
}

.plenty-brand {
  height: 30px;
  margin: 2em 0 0.2em;
}

.static-link-list {
  margin-bottom: -15px;
}

.static-link-list a {
  margin-right: 10px;
}