.cmp-product-thumb {
  padding: .8rem;
  width: 100%;
  overflow: hidden;
  border: 1px solid $gray-lighter;
  background-color: #fff;
  margin-bottom: $grid-gutter-width/2;

  .thumb-background {
    position: absolute;
    top: 0px; // -1, -1, -2, -1 ????
    right: 0px;
    bottom: 0px;
    left: 0px;
    background-color: #fff;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    border: 1px solid transparent;

    transform-origin: top right;
    transform: scaleX(1) scaleY(1);

    transition: all 200ms ease-in-out;
  }

  .thumb-inner {
    position: relative;
    height: 100%;
    transition: all 400ms;
  }

  .thumb-image {
    position: relative;
    width: 120px;
    display: inline-block;
    vertical-align: top;
    cursor: pointer;
  }

  .main-image {
    @include img-contain();
    position: absolute;
    background-color: #fff;
    display: flex;
    align-items: center;
  }

  //TODO create separate mixin
  .special-tags {
    z-index: 100;
    position: absolute;
    top: 0;
    right: auto;
    bottom: auto;
    left: 0;

    padding: .5rem;

    .special-tag,
    .tag {
      display: block;
      padding: .3em .5em .2rem;
      font-size: .9rem;
      font-weight: 300;
      box-shadow: 0 0 3px 0 rgba(0, 0, 0, .4);
    }
  }

  .thumb-content {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: calc(100% - 120px);
    height: auto;
    padding: 0 .8rem;
    margin-left: -.3em;
    background-color: #fff;
    margin-bottom: 12px !important;
    transition: all 200ms ease-in-out;
  }

  .thumb-title {
    display: block;
    position: relative;
    text-align: left;
    font-weight: 700;
    color: $gray;
    line-height: 1.1em;
    &:hover {
      color: $brand-primary;
    }
  }

  .thumb-meta {
    margin-top: .5rem;
    text-align: left;

  }

  .crossprice {
    font-size: .81em;
    font-weight: 700;
    color: $gray-light;
  }

  .price {
    color: $gray;
    line-height: 1.1;
    font-weight: 700;
    font-size: 1.2rem;
  }

  .thumb-buy {
    display: block;
  }

  .btn-group {
    position: absolute;
    bottom: 0;
    left: calc(120px + 0.8rem);
    z-index: 1000;
    transition: all .3s ease;
    opacity: 1;
  }

  @include media-breakpoint-up(sm) {
    padding: 0;
    margin-bottom: $grid-gutter-width;

    .thumb-background {
      display: block;
    }

    .thumb-scroll-cmd {
      flex: 0 0 auto;
      text-align: center;
      padding: .4rem;
    }

    .thumb-btn-buy {
      min-height: 2rem;
    }

    .thumb-content {
      padding: .8rem;
      width: 100%;
      margin: 0;
    }

    .btn-group {
      bottom: .8rem;
      left: auto;
      right: .5rem;
    }

    .thumb-title {
      font-weight: 400;

      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      line-height: 1.5em;
      max-width: 100%;
    }

    .thumb-image {
      width: 100%;
    }

    .img-fluid {
      transition: transform .4s cubic-bezier(0, 0, .26, 1), opacity .3s cubic-bezier(0, 0, .26, 1), -webkit-transform .3s cubic-bezier(0, 0, .26, 1);
    }

    &:hover {
      z-index: 1000;
      .img-fluid {
        transform: scale(1.05);
      }

      .owl-nav [class*='owl-'] {
        opacity: 1;
      }
    }
  }

  @include media-breakpoint-up(md) {

  }

  @include media-breakpoint-up(lg) {
    .btn-group {
      transition: all .3s ease;
      opacity: 0;
    }
    &:hover {
      .btn-group {
        transition: all .3s ease;
        opacity: 1;
      }
    }
  }

}