        0: (
                x: 0,
                y: 0
        ),
        05: (
                x: ($spacer-x * 0.5),
                y: ($spacer-y * 0.5)
        ),
        1: (
                x: $spacer-x,
                y: $spacer-y
        ),
        2: (
                x: ($spacer-x * 1.5),
                y: ($spacer-y * 1.5)
        ),
        3: (
                x: ($spacer-x * 3),
                y: ($spacer-y * 3)
        )
) !default;
