.featured-sale {
  display: block;
  height: 100%;
  overflow: hidden;
  color: $gray-dark;

  @include img-cover();

  .featured-category-inner {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    border: 1px solid $brand-primary;
    border-bottom: 0px;
    display: flex;
    flex-flow: column;
    justify-content: flex-end;

    padding: 1.5rem;
  }

  .featured-category-headline {
    background-color: rgba(white, .5);
    max-width: 10rem;
    padding: .5em;
    text-align: right;
    font-weight: normal;
    display: inline-block;
    width: fit-content;
    align-self: flex-start;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, .1);
  }

  .featured-category-cta {
    display: inline-block;
    background-color: rgba(white, .5);
    padding: .5em;
    text-align: right;
    width: fit-content;
    align-self: flex-start;

    .fa {
      margin-left: .5rem;
    }
  }
}